<template>
  <form @submit="onSubmit" class="add-form">
    <!--we will have data for each field, text field, date field, so we add that as data in the script...data-->
    <div class="form-control">
      <label>Task</label>
      <input type="text" v-model="text" name="text" placeholder="Add Task" />
    </div>
    <div class="form-control">
      <label>Day & Time</label>
      <input
        type="text"
        v-model="day"
        name="day"
        placeholder="Add Day & Time"
      />
    </div>
    <div class="form-control form-control-check">
      <label>Set Reminder</label>
      <input type="checkbox" v-model="reminder" name="reminder" />
    </div>

    <input type="submit" value="Save Task" class="btn btn-block" />
  </form>
</template>

<script>
  export default {
    name: 'AddTask',
    //data function which will return an object, and we bind this values with the inputs from the form  with the directive v-model
    data() {
      return {
        //the default of those inputs
        text: '',
        day: '',
        reminder: false
      }
    },
    methods: {
      onSubmit(e) {
        //preventing the default submiting
        e.preventDefault()

        //validating the input in form and display a message if not valid
        if(!this.text) {
          alert('Please add a task')
          return
        }
        //if the validation passes we construct a new task
        const newTask = {
          //creating id
          // id: Math.floor(Math.random() * 100),
          text: this.text,
          day: this.day,
          reminder: this.reminder
        }
        //add a task and pass the actual task
        this.$emit('add-task', newTask)
        //also we clear the form
        this.text = ''
        this.day = ''
        this.reminder = false
      }
    },
  }
</script>

<style scoped>
  .add-form {
    margin-bottom: 40px;
  }
  .form-control {
    margin: 20px 0;
  }
  .form-control label {
    display: block;
  }
  .form-control input {
    width: 100%;
    height: 40px;
    margin: 5px;
    padding: 3px 7px;
    font-size: 17px;
  }
  .form-control-check {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .form-control-check label {
    flex: 1;
  }
  .form-control-check input {
    flex: 2;
